body {
  padding: 12px;
  margin: 20px;
  background-color: rgb(4, 130, 248);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.span-tag {
  font-size: 13px;
}

.col-sm {
  margin: 20px;
  padding: 20px;
  background-color: azure;
  border-radius: 10px;
  overflow: hidden;
}

.disabled-text {
  color: rgb(150, 155, 155);
}

.aling-center-text {
  text-align: center;
}

.disable-heading-text {
  font-weight: bold;
  font-size: 14px;
  color: rgb(161, 156, 156);
  letter-spacing: 3px;
}

.center-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-edits {
  margin-top: 10px;
  border-radius: 100px;

}

.col-sm:hover {
  position: relative;
  top: -5px;
}
